.App {
  background-color: rgba(30, 30, 30);
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: center;
}
.fa-bars {
  font-size: 30px;
  position: fixed;
  top: 2%;
  left: 5%;
  z-index: 10;
}

.section-bg {
  position: relative;
  z-index: 2;
}
section {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-left: 2%;
  margin-right: 0;
  min-height: 100vh;
  overflow: hidden;
  opacity: 0;
  padding: 2% 0;
  adding-left: 2%;
  position: relative;
  -webkit-transition: all .7s ease-out;
  -o-transition: all .7s ease-out;
  transition: all .7s ease-out;
  width: 100vw;
  z-index: 3;
}
.appear {   /*  animation  */
  opacity: 1;
  margin-left: 0;
  padding-left: 0%;
  width: 100vw !important;
}

h1 {
  color: #448888;
  font-family: 'Audiowide';
  font-size: 50px;
  padding: 10px 0;
}
h2 {
  color: #448888;
  font-family: 'Audiowide';
}
h3 {
  color: #559999;
  font-family: 'raleway';
  margin: 0;
  padding: 0;
}
p {
  font-family: 'raleway';
}

button {
  background: none;
  border: 2px solid #889999;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0px 10px 1px #111;
          box-shadow: 0px 0px 10px 1px #111;
  color: #889999;
  cursor: pointer;
  font-size: 20px;
  margin: auto;
  padding: 3px 6px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  white-space: nowrap;
}
button:hover {
  border: 2px solid #226666;
  color: #339999;
  letter-spacing: 2px;
}

/*pulse-anim */
.pulse-anim {
}
.pulse-anim-1 {
  background: none;
  color: transparent;
  text-shadow: 0px 0px 20px black;
  -webkit-transition: all 1.6s ease-in;
  -o-transition: all 1.6s ease-in;
  transition: all 1.6s ease-in;
}
.pulse-anim-2 {
  color: #228888;  
  -webkit-transition: all 1.5s ease-out;  
  -o-transition: all 1.5s ease-out;  
  transition: all 1.5s ease-out;
}


/* thanks */
.thanks {
  background: rgba(150, 200, 200, 0.7);
  color: azure;
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  height: 80px;
  text-decoration: underline;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
}
.thanks div {
  margin: auto;
}

@media (max-width: 600px) {
  h1 {
    font-size: 40px;
  }
}


