.overlay {
	background: rgba(20, 20, 20, 0.9);
	display: -ms-flexbox;
	display: flex;
	opacity: 0;
	position: fixed;
	top: 10%;
	right: 0;
	bottom: 10%;
	-webkit-transition: all 0.5s ease;
	-o-transition: all 0.5s ease;
	transition: all 0.5s ease;
	visibility: hidden;
	white-space: nowrap;
	width: 0%;
	z-index: 10;
}
.overlay-text-box, .overlay-img-box {
	-ms-flex-order: 1px solid white;
	    order: 1px solid white;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	height: 100%;
	padding: 50px;
	width: 50%;
}
.overlay-text-box {
	overflow: auto;
	padding-left: 150px;
	text-align: left;
	white-space: pre-wrap;
}
.overlay-text-box p {
	margin-bottom: 36px;
	width: 80%;
}
.overlay-text-box li {
	list-style: circle;
	margin: 5px 20px;
}
.overlay-img-box {
}
.overlay img {
	-webkit-box-shadow: 0px 0px 10px 1px #111;
	        box-shadow: 0px 0px 10px 1px #111;
	width: 700px;
	max-width: 98%;
}
.overlay-img-pre {
	display: -ms-inline-flexbox;
	display: inline-flex;
	height: 30%;
	margin: 5px;
	overflow-y: auto;
}
.overlay-img-pre img {
	height: 135px;
	margin: 2px;
	width: 240px;
}

.light-border {
	-webkit-box-shadow: 0px 0px 10px 1px #448888;
	        box-shadow: 0px 0px 10px 1px #448888;
	border: 1px solid #339999;
	border-radius: 50%;
	margin: 20px auto;
	height: 0;
	width: 60%;
}
.overlay i {
	color: azure;
	cursor: pointer;
	font-size: 24px;
	position: absolute;
	top: 2%;
	right: 1%;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
.overlay i:hover {
	color: tomato;
}

.overlay-btn {
	border: 2px solid #779999;
	color: #779999;
}

@media (max-width: 1000px) {
	.overlay {
		background: rgba(30, 30, 30, 0.9);
		display: inline-block;
		opacity: 0;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		overflow: auto;
		-webkit-transition: all 0.5s ease;
		-o-transition: all 0.5s ease;
		transition: all 0.5s ease;
		visibility: hidden;
		white-space: nowrap;
		width: 0%;
		z-index: 10;
	}
	.overlay-text-box, .overlay-img-box {
		-ms-flex-order: 1px solid white;
		    order: 1px solid white;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		height: auto;
		padding: 50px;
		width: 100%;
	}
	.overlay-text-box p {
		width: 98%;
	}
}



